export class InvoiceTransactionSummaryReportViewController extends ReportBaseViewController {
	constructor( options = {} ) {
		_.defaults( options, {} );

		super( options );
	}

	initReport( options ) {
		this.script_name = 'InvoiceTransactionSummaryReport';
		this.viewId = 'InvoiceTransactionSummaryReport';
		this.context_menu_name = $.i18n._( 'Transaction Summary' );
		this.navigation_label = $.i18n._( 'Saved Report' );
		this.view_file = 'InvoiceTransactionSummaryReportView.html';
		this.api = TTAPI.APIInvoiceTransactionSummaryReport;
	}

	getCustomContextMenuModel() {
		return { include: ['default'] };
	}

	// Overriding empty ReportBaseViewController.processFilterField() called from base.openEditView to provide view specific logic.
	processFilterField() {
		for ( var i = 0; i < this.setup_fields_array.length; i++ ) {
			var item = this.setup_fields_array[i];
			if ( item.value === 'invoice_transaction_type_id' ) {
				item.value = 'filter';
			}
		}
	}

	onFormItemChangeProcessFilterField( target, key ) {
		var filter = target.getValue();
		this.visible_report_values[key] = { invoice_transaction_type_id: filter };
	}

	setFilterValue( widget, value ) {
		widget.setValue( value.invoice_transaction_type_id );
	}

}
